<template>
  <div class="main-page">
  <navigationComponent></navigationComponent>
    <h2><strong>学习随记</strong></h2>
    <router-view v-bind:message="msg"></router-view> <!-- 路由传值 -->
    <router-link :to="{path:'/upup/blog'}" class="goChild">
      <div class="getin" v-for="name in files" :key="name" @click="enterArticle($event)">{{name}}</div> <!-- 遍历文件名 -->
    </router-link>
    <div id="pageContainer">
      <span class="page" :class="{'active': cur == index}" v-for="(number, index) in pages" :key="index" @click="goPage($event)">{{index += 1}}</span>
    </div>
  </div>
</template>

<script src="../../static/layui/layui.js"></script>
<script>
import Vue from 'vue'
import navigationComponent from './navigationComponent.vue'
import blogComponent from './blogComponent.vue' // 引用子组件
export default {
  name: 'upup',
  components: {
    navigationComponent,
    blogComponent // 引用子组件
  },
  data () {
    const filesMD = require.context('../../static/markdown', false, /.md$/).keys() // 获取MD文件夹内文件名

    let filesName = [] // 新建数组用来存放所有文件名
    for (var i = 0; i < filesMD.length; i++) {
      var n = filesMD[i].substring(2) // 去除路径标识
      n = n.substring(0, n.lastIndexOf('.')) // 去除后缀名
      filesName.push(n) // 剩下完整文件名放入数组
    }
    let pages = [] // 总页面数组
    let totalFile = filesName.length // 初始化文件总数
    if (totalFile > 8) { // 每八个文件分别加入pages数组
      for (var s = 0; s < totalFile; s += 8) {
        pages.push(filesName.slice(s, s + 8)) // 添加数组
        console.log(pages)
      }
    }
    console.log(filesName)
    let showList = pages[0]

    return {
      msg: '', // 传递点击事件获取的文件名至子组件
      files: showList, // 绑定数组内容至html
      pages: pages, // 分页数组赋值
      cur: 0
    }
  },
  mounted () {
  },
  methods: {
    tip: function () { // 返回顶部按钮
      this.$refs.goTop.style.display = 'none'
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      })
    },
    enterArticle: function (e) {
      const nameOfMD = require.context('../../static/markdown', false, /.md$/).keys() // 获取MD文件夹内文件名
      const target = e.target.innerHTML // 获取当前点击事件的html标签内字符
      this.msg = target // 改变data中的值
      sessionStorage.setItem('markdown', target) // 存放至sessionStorage，避免刷新后数值消失
      return nameOfMD
    },
    goPage: function (e) {
      const pageIndex = e.target.innerHTML // 获取当前点击事件的html标签内字符
      let index = pageIndex - 1 //得到索引值
      this.files = this.pages[index]
      this.cur = index // 页数索引值
      // this.files = JSON.parse(JSON.stringify(this.showList))
      console.log(this.showList)
    }
  }
}
</script>

<style scoped>
.main-page {
  position: fixed;
  top: 0;
  bottom: 0;
  margin: 0 auto;
  padding: 50px 10% ;
  width: 100%;
  height: auto;
  overflow: auto;
}
h2 {
  text-align: center;
}
.article {
  height: 100px;
  overflow-y: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;

}
.article > p {
  display: block;
  margin: 15px 0;
}
.article-title {
  font-size: 18px;
}
a {
  color: #01AAED;
}
a:hover {
  color: #777;
}
#app {
  margin-top: 2000px;
}
.gray-gb {
  background-color: #c2c2c2;
}
.goChild {
  display: block;
  margin: 5px auto 35px;
  width: fit-content;
  height: 380px;
  cursor: default;
}
.goChild:hover {}
.getin {
  margin: 25px auto;
  width: fit-content;
  border-bottom: 1px solid #000000;
  text-align: center;
  text-decoration: solid;
  font-size: 18px;
  color: #777;
  cursor: pointer;
}
.getin:hover {
  color: #C0C4CC;
}
#pageContainer {
  position: relative;
  margin: 0 auto;
  padding: 6px;
  width: fit-content;
  height: 38px;
  border-radius: 19px;
  text-align: center;
  background-color: #ddd;
}
.page {
  display: inline-block;
  margin: 0 3px;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  box-sizing: border-box;
  line-height: 26px;
  color: #000;
  text-align: center;
  cursor: pointer;
}
.page:hover {
  background-color: #00A0DC;
  color: #fff;
}
.active {
  background-color: #00A0DC;
  color: #fff;
}
</style>



