<template>
<div class="container">
  <navigationComponent></navigationComponent>
  <div>
    <h2><strong>前端博客</strong></h2>
      <div class="gray-left-line">
        <p>欢迎参观</p>
      </div>
      <div class="gray-left-line">
        <p>这是我灵机一动，自己动手做的个人主页，现在挂在Github Pages上，框架用的是Vue和Layui，这里能记录很多东西，但是还需要时间来丰富内容</p>
      </div>
      <div class="gray-left-line">
        <p>虽然内容不多，体验也不够好，但是我会努力改善，将我能想到的加入到这里，我学习前端以来已经半年有余了，学习了很多也收获了很多，坑也踩了很多，一个个困难也都解决了，但是前路漫漫，还有更多的坑和困难在前方阻挡，在这里也能更好地把之前遇到的问题和坑记录下来，提醒自己，学而不思则罔，思而不学则殆，以前没有这种好习惯，但是现在开始慢慢养成，也不迟，这也是博客的意义
        </p>
      </div>
      <div class="gray-left-line">
        <p>当然这个博客不仅仅限于前端，有好玩的有趣的一样都可以记录，等域名到期想要换一个好记的域名，长期经营下去
        </p>
      </div>
  </div>
  <div><p>正在开发中，敬请期待!</p></div>
  <div id="inner" class="container">
    <router-link to="/upup"><button class="layui-btn layui-btn-normal gray-gb">看看其它</button></router-link>
  </div>
</div>
</template>

<script>
import navigationComponent from './navigationComponent.vue'
export default{
  name: 'Page2',
  components: {
    navigationComponent
  }
}
</script>

<style scoped>
.square {
  width: 100px;
  height: 100px;
  border: 22px solid;
  border-image: url(../assets/QQQR.png) repeat;
  border-color: #893615 transparent transparent transparent;
}
.container {
  width: 80%;
}
.layui-timeline-axis {
  color: #000000;
}
</style>



