<template>
  <div class="article-detail layui-bg-gray">
    <div></div>
    <!-- <div v-html="newMD"></div> 不支持MD格式 -->
    <markdown-it-vue class="article" :content="newMD"/>
    <router-link :to="{path: '/upup'}">
      <button class="goBackBtn"><i class="layui-icon layui-icon-prev"></i> 返回</button>
    </router-link>
  </div>
</template>

<script>
import VueMarkdown from 'vue-markdown'
import MarkdownItVue from 'markdown-it-vue' // HTML标签内引用MD文件
import 'markdown-it-vue/dist/markdown-it-vue.css' // MD文件样式
import Vue from 'vue'
export default{
  components: {
    MarkdownItVue
  },
  name: 'blog',
  props: ['message'], // 父组件传值
  data () {
    var setOne = sessionStorage.getItem('markdown') // 获取文件信息
    const url = '../../static/markdown/' + setOne + '.md' // 获取文件路径
    this.$axios.get(url).then((response) => { // 获取当前路径中的所有文件名称
      this.newMD = response.data
    })
    const filesMD = require.context('../../static/markdown', false, /.md$/).keys() // 获取MD文件夹内文件名

    function getFileName (o) { // 去除文件路径名
      console.log(filesMD[0].substring(2)) // substring(2)代表从字符串的第二个字符处截取后半段字符
      var pos = o.lastIndexOf('/') // 定位最后一个斜杠出现的位置
      return o.substring(pos + 1)
    }
    return {
      newMD: ''
    }
  },
  created: function () {
  },
  methods: {
  }
}
</script>

<style scoped>
  .article-detail {
    z-index: 10;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    padding: 80px 0;
    width: 100%;
    overflow: auto;
  }
  .article {
    position: relative;
    margin: 0 auto;
    width: 80%;
  }
  .goBackBtn {
    display: block;
    position: fixed;
    left: 20px;
    bottom: 20px;
    padding: 5px;
    border: none;
    border-radius: 2px;
    background-color: rgba(255,255,255,0);
    font-size: 16px;
    color: #777777;
    white-space: nowrap;
    text-align: center;
    cursor: pointer;
  }
  .layui-icon-prev::before {
  }
</style>



